.loader
{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index: 10;
  background-color: black;
  opacity: 0.8;
  visibility: hidden;
}
#Layer_1 {
  width: 30%;
  height: 30%;
  position: absolute;
  z-index: 11;
  bottom: 35%;
  left: 35%;
  opacity: 1;
  
}

.hex {
  fill:#9400D4;
  -webkit-animation: fadein 3.3s infinite;
          animation: fadein 3.3s infinite;
  opacity: 0;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}

.hex:nth-child(2) {
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
  fill:#1d85bb;
}

.hex:nth-child(3) {
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
  fill:#ff0071;
}

.hex:nth-child(4) {
  -webkit-animation-delay: .6s;
          animation-delay: .6s;
  fill:#009997;
}

.hex:nth-child(5) {
  -webkit-animation-delay: .8s;
          animation-delay: .8s;
  fill: #B4FD00;
}

.hex:nth-child(6) {
  -webkit-animation-delay: 1.0s;
          animation-delay: 1.0s;
  fill:#FF8000;
}

.hex:nth-child(7) {
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
  fill:#B08C77;

}

@-webkit-keyframes fadein {
  0%, 100% {
    -webkit-transform: scale(0.001,0.001);
            transform: scale(0.001,0.001);
    opacity: 0;
  }
  
  33%, 66% {
    -webkit-transform: scale(.9,.9);
            transform: scale(.9,.9);
    opacity: 1;
  }
}

@keyframes fadein {
  0%, 100% {
    -webkit-transform: scale(0.001,0.001);
            transform: scale(0.001,0.001);
    opacity: 0;
  }
  
  33%, 66% {
    -webkit-transform: scale(.9,.9);
            transform: scale(.9,.9);
    opacity: 1;
  }
}


