
.timer
{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1.8%;
  margin-bottom: 0px;
}
span#procent {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 30px;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}



.canvas-wrap {
  position: relative;
  margin-right: 3%;
  margin-left: 3%;
  width: 250px;
  height: 250px;
}
.canvas
{
    height: 250px;
    width: 250px;
}
@media only screen and (max-width:1366px)
{
  span#procent
  {
    font-size: 30px;
  }
}
@media only screen and (max-width:1000px)
{
  span#procent
  {
    font-size: 28px;
  }
  .canvas-wrap
  {
    width: 210px;
    height: 210px;
  }
  .canvas
  {
    width: 210px;
    height: 210px;
  }
}
@media only screen and (max-width:860px)
{
  span#procent
  {
    font-size: 26px;
  }
  .canvas-wrap
  {
    width: 170px;
    height: 170px;
  }
  .canvas
  {
    width: 170px;
    height: 170px;
  }
}
@media only screen and (max-width:600px)
{
  span#procent {
  font-size: 23px;
}
  .canvas-wrap
  {
    width: 170px;
    height: 170px;
  }
  .canvas
  {
    height: 170px;
    width: 170px;
  }

}
@media only screen and (max-width:470px)
{
  span#procent {
  font-size: 20px;
}
  .canvas-wrap
  {
    position: relative;
  margin-top: 0;
  margin-right: 0;
  margin-left: 0;
  width: 150px;
  height: 150px;
  }
  .canvas
  {
    height: 150px;
    width: 150px;
  }

}